  function priorityFn(name: string) {
    const rank = name.includes("真人")
      ? 1
      : name.includes("電子")
      ? 2
      : name.includes("RSG")
      ? 2
      : name.includes("體育")
      ? 3
      : name.includes("鑫寶")
      ? 3
      : name.includes("棋牌")
      ? 4
      : name.includes("ZG")
      ? 5
      : name.includes("捕魚")
      ? 5
      : name.includes("愛發")
      ? 5
      : name.includes("彩票")
      ? 6
      : name.includes("GR")
      ? 6
      : 0;
    return rank;
  }
  const SortApiBalance = computed(() => {
    const sort = Object.keys(ApiBalance.value)
      .map((key) => [key, ApiBalance.value[key]])
      .sort((a, b) => {
        console.log(a[0], b[0]);
        return priorityFn(a[0].toString()) - priorityFn(b[0].toString());
      });
    return sort;
  });
